@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

@font-face {
  font-family: Alice in Wonderland;
  src: url(/memory-game/static/media/Alice_in_Wonderland_3.0507cab2.ttf);
}

body {
  font-family: 'Alice in Wonderland';
  letter-spacing: 3px;
  background-image: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.1) ), url(/memory-game/static/media/bg-2.3564551c.jpg);
  background-size: cover;
  margin: 0; 
  height: 100%;
  overflow: hidden
}

h1 {
  font-size: 3.4rem;
  color: black;
  text-shadow: 2px 3px #cfc8c8;
}

p {
  font-size: 30px;
}

.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid rgb(0, 0, 0);
  padding: 6px 12px;
  border-radius: 4px;
  color: black;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  font-family: 'Alice in Wonderland';
  letter-spacing: 1px;
  width: 150px;
}
button:hover {
  background: #03587d83;
  color: #fff;
}

button:active {
  box-shadow: 0 2px #666;
  transform: translateY(2px);
}

.card-grid {
  margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
.card {
    position: relative;
}

.card img {
    width: 100%;
    display: block;
    border: 2px solid #fff;
    border-radius: 6px;
}

/* Front */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
}

.flipped .front {
    transform: rotateY(0deg);
}

/* Back */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
    filter: brightness(85%) 
}

.flipped .back {
    transform: rotateY(90deg);
    transition: 0s;
}
